export default {
  name: 'SideBar',

  data() {
    return {
      sidebarIconSize: 30
    };
  },

  props: {
    displayVersion: {
      type: String,
      default: "simple"
    },
    user: {
      type: Object,
      default: {
        userName: "User Name",
        nickName: "User Nick Name"
      }
    }
  },
  methods: {
    closeBar() {
      this.$emit("close-side-bar-event");
    },

    switchMode(mode) {
      var data = {
        view: mode
      };
      this.$emit("switch-event", data);
    }

  },

  mounted() {
    if (window.innerWidth < 992) {
      this.sidebarIconSize = 20;
    }
  }

};